import React, { useState } from "react";
import styled from "styled-components";

import AddRuleButton from "../Atoms/AddRuleButton";
import RuleModal from "../Atoms/RuleModal";

const Container = styled.div`
    display: flex;
    width: 100%;
    justify-content: space-around;
`;

export default function AddRuleController(props) {
    const [showModalBox, setShowModalBox] = useState(false);
    const { repositoryName="mock" } = props;
    return (
        <>
            <Container>
                <AddRuleButton
                    onClick={() => {
                        setShowModalBox(!showModalBox);
                    }}
                />
            </Container>

            <RuleModal
                {...props}
                visibility={showModalBox ? "visible" : "hidden"}
                onClick={() => {
                    setShowModalBox(!showModalBox);
                }}
                repositoryName={repositoryName}
            />
        </>
    );
}
