import React from "react";
import {
    Field,
    Control,
    Label,
    Radio,
    Select
} from "react-bulma-components/lib/components/form";

function RadioGroup(value, setValue, options) {
    return (
        <Control>
            {options.map(item => {
                return (
                    <Radio
                        value={item}
                        checked={item === value}
                        onChange={event => setValue(event.target.value)}
                    >
                        {item}
                    </Radio>
                );
            })}
        </Control>
    );
}

function ColumnSelect(value, setValue, options, objectType, baseURI) {
    return (
        <Control>
            <Select
                value={value}
                disabled={objectType === null}
                onChange={event => {
                    setValue(event.target.value);
                }}
            >
                {options.map(item => {
                    return (
                        <option value={item}>
                            {objectType === "Object(Entity)"
                                ? baseURI + "/<"
                                : "<"}
                            {item}
                            {">"}
                        </option>
                    );
                })}
            </Select>
        </Control>
    );
}

export default function ObjectSelector(props) {
    const {
        objectColumn = "mock",
        setObjectColumn = console.log,
        setObjectType = console.log,
        objectType = "Literal(String)",
        availableObjectColumns = ["id", "name", "mock"],
        baseURI = "http://mock.com/<mock>"
    } = props;

    const Radios = RadioGroup(objectType, setObjectType, [
        "Literal(String)",
        "Object(Entity)"
    ]);

    const Selects = ColumnSelect(
        objectColumn,
        setObjectColumn,
        availableObjectColumns,
        objectType,
        baseURI
    );

    return (
        <Field kind="group">
            <Control>
                <Label>Object Type</Label>
                {Radios}
            </Control>

            <Control>
                <Label>Column</Label>
                {Selects}
            </Control>
        </Field>
    );
}
