import React, { useState } from "react";
import axios from "axios";
import styled from "styled-components";
import { Box, Content, Button } from "react-bulma-components";
import {
    Field,
    Control,
    Label,
    Input,
    Help,
    Select
} from "react-bulma-components/lib/components/form";

const ModalBoxContainer = styled.div`
    visibility: ${props => props.visibility || "visible"};
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`;

const Background = styled.div`
    background: #4e4e4e;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    position: absolute;
`;

const ModalBox = styled(Box)`
    width: 40%;
    z-index: 2;
`;

const submitChange = (baseurl, entityIdentifier, repositoryName, props) => {
    axios
        .post(
            window.API_ENDPOINT +
                "/repository/" +
                repositoryName +
                "/rdf/set_identifier/",
            {identifier: entityIdentifier, baseurl: baseurl}
        )
        .then(response => {
            window.location.reload()
        })
}

export default function EditMenu(props) {
    const {
        baseurl = "http://www.example.com/editmenu",
        entityIdentifier = "id",
        availableIdentifier = ["id", "name"],
        repositoryName
    } = props;

    const [baseURL_form, setbaseURL_form] = useState(baseurl);
    const [entityIdentifier_form, setIdentifier_form] = useState(
        entityIdentifier
    );

    return (
        <ModalBoxContainer visibility={props.visibility}>
            <Background onClick={props.onClick} />
            <ModalBox>
                <Content>
                    <h3>Edit Base URL</h3>
                    <hr />
                    <Field>
                        <Label>Base URL</Label>
                        <Control>
                            <Input
                                value={baseURL_form}
                                onChange={event => {
                                    setbaseURL_form(event.target.value);
                                }}
                            />
                        </Control>
                        <Help>
                            This will be the url prefixing the entities.
                        </Help>
                    </Field>
                    <br />
                    <Field>
                        <Label>Entity Identifier</Label>
                        <Control>
                            <Select
                                value={entityIdentifier_form}
                                onChange={event => {
                                    setIdentifier_form(event.target.value);
                                }}
                            >
                                {availableIdentifier.map(item => {
                                    return <option value={item}>{"<" + item + ">"}</option>;
                                })}
                            </Select>
                        </Control>
                        <Help>
                            This will be the name of each entity(row) parsed
                            from scraped data.
                        </Help>
                    </Field>
                    <br />
                    <Field kind="group">
                        <Control>
                            <Button type="primary" onClick={() => submitChange(baseURL_form, entityIdentifier_form, repositoryName, props)}>Confirm</Button>
                        </Control>
                        <Control>
                            <Button color="link" onClick={props.onClick}>
                                Cancel
                            </Button>
                        </Control>
                    </Field>
                </Content>
            </ModalBox>
        </ModalBoxContainer>
    );
}
