import React from "react";
import BaseURLText from "../Atoms/BaseURLText";
import EditURLController from "../Molecules/EditURLController";

export default function BaseURLArea(props) {
    const {
        baseurl = "http://www.example.com",
        entityIdentifier = "id",
        availableIdentifier = ["<id>", "<name>"],
        repositoryName
    } = props;

    return (
        <span>
            <BaseURLText baseurl={baseurl} entityIdentifier={entityIdentifier} />
            <EditURLController baseurl={baseurl} entityIdentifier={entityIdentifier} availableIdentifier={availableIdentifier} repositoryName={repositoryName}/>
        </span>
    );
}
