import React, { useState } from "react";
import axios from "axios";
import { Content } from "react-bulma-components";

import BaseURLArea from "../Molecules/BaseURLArea";
import RulesTable from "../Molecules/RulesTable";
import AddRuleController from "../Molecules/AddRuleController";

const getRdfRest = (stateSetter, repositoryName) => {
    axios
        .get(window.API_ENDPOINT + "/repository/" + repositoryName + "/rdf/")
        .then(response => {
            stateSetter(response.data);
            console.log(response.data);
        })
        .catch(error => {
            console.log(error);
            stateSetter(false);
        });
};

export default function LinkedDataExportArea(props) {
    const [rdfRest, setRdfRest] = useState(null);

    if (rdfRest === null) {
        getRdfRest(setRdfRest, props.repositoryName);
        return <div>Loading</div>;
    }
    const { base_url, entity_uri, parsed_column } = rdfRest;
    const { repositoryName } = props;

    return (
        <div>
            <Content>
                <h1>Linked Data Construction Tool</h1>
                <br />
                <dl>
                    <dt>
                        <h3>Base URL - [ {props.repositoryName} ] </h3>
                    </dt>
                    <dd>
                        <BaseURLArea
                            baseurl={base_url}
                            entityIdentifier={entity_uri}
                            availableIdentifier={parsed_column}
                            repositoryName={repositoryName}
                        />
                    </dd>
                </dl>

                <hr />
                <h3>Created Rules</h3>
                <RulesTable repositoryName={repositoryName} />

                <br />
                
                <AddRuleController subject={base_url} entityIdentifier={entity_uri} availableIdentifier={parsed_column} repositoryName={repositoryName}/>
            </Content>
        </div>
    );
}
