import React from "react";
import { Content } from "react-bulma-components";

export default function DocumentationArea(props) {
    const { doc } = props;

    return (
        <div
            style={{
                width: "50%",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                margin: "1em 1em 0em",
                flexShrink: "0"
            }}
        >
            <b>Documentation</b>

            <div
                style={{
                    borderStyle: "solid",
                    borderWidth: "1px",
                    padding: "1em 1em 1em 1em",
                    margin: "1em 0 0",
                    height: "100%",
                    width: "100%",
                    overflow: "scroll"
                }}
            >
                <Content>
                    { doc && <h4>{doc.label}</h4>}
                    {doc ? Object.keys(doc).map(key => (
                        <p>
                            <b>{key}</b>: {doc[key]}
                        </p>
                    )) : "Please Select Vocabulary and Word."}
                </Content>
            </div>
        </div>
    );
}