import React from "react";
import styled from "styled-components";
import { Box, Button, Content } from "react-bulma-components";

import Navbar from "../../Template/Navbar";
import LinkedDataExportArea from "../Orgnaisms/LinkedDataExportArea";

const DisplayArea = styled(Box)`
    margin: 3em;
`;

const handleExport = (repositoryName, format) => {
    window.open(window.API_ENDPOINT +
                    "/repository/" +
                    repositoryName +
                    "/rdf/export/" + format , "_blank")
};

export default function LinkedDataExport(props) {
    let repositoryName = props.match
        ? props.match.params.repositoryName
        : "mock";

    return (
        <div>
            <Navbar />
            <section
                className="section"
                style={{ padding: "0", height: "100%" }}
            >
                <DisplayArea>
                    <LinkedDataExportArea repositoryName={repositoryName} />
                    <hr />
                    <Content style={{"width": "100%"}}>
                        <h3>Download linked data file</h3>
                    </Content>
                    <div style={{display: "flex", justifyContent: "space-around"}}>
                        <Button size="medium" onClick={() => {handleExport(repositoryName, "n3")}}>Export as N3 format</Button>
                        <Button size="medium" onClick={() => {handleExport(repositoryName, "turtle")}}>Export as Turtle format</Button>
                        <Button size="medium" onClick={() => {handleExport(repositoryName, "xml")}}>Export as RDF/XML format</Button>
                    </div>
                </DisplayArea>
            </section>
        </div>
    );
}
