import React, { useState } from "react";
import axios from "axios";
import styled from "styled-components";

import { Content, Button } from "react-bulma-components";

import ModalBox from "../Atoms/ModalBox";
import SelectColumn from "../Atoms/SelectColumn";
import DocumentationArea from "../Atoms/DocumentationArea";

const ColumnsContainer = styled.div`
    display: flex;
    height: 80%;
    justify-content: space-between;
    margin: 1em 0em 0em 0em;
`;

const getDocumentation = (stateSetter, repositoryName) => {
    axios
    .get(window.API_ENDPOINT + "/repository/" + repositoryName + "/rdf/vocabulary/")
    .then(response => {
        stateSetter(response.data);
    })
    .catch(error => {
        console.log(error);
        stateSetter({
            foaf: {
                "foaf:Bag": {
                    comment: "this is a bag",
                    label: "Bag"
                },
                "foaf:Bag2": {
                    comment: "this is a bag number two",
                    label: "Bag"
                },
                "foaf:Bag3": {
                    comment: "this is a bag number three",
                    label: "Bag"
                }
            },
            rdf: {
                "rdf:Bag": {
                    comment: "whatever1",
                    label: "what"
                },
                "rdf:Bag2": {
                    comment: "whatever2",
                    label: "nani"
                },
                "rdf:Bag3": {
                    comment: "foo",
                    label: "bar"
                }
            }
        });
    });
}

export default function LinkedDataVocabularyModal(props) {
    const [documentation, setDocumentation] = useState(null);
    const [vocab, setVocab] = useState(null);
    const [word, setWord] = useState(null);

    const { visibility, setVisibility, setPredicate, repositoryName } = props;
    
    if(documentation === null){
        getDocumentation(setDocumentation, repositoryName)
        return "loading"
    }

    console.log(documentation)

    const vocabs = Object.keys(documentation);
    const words = vocab ? Object.keys(documentation[vocab]) : [];

    const doc = vocab && words ? documentation[vocab][word] : undefined;

    

    const handleSetPredicate = () => {
        setPredicate({"vocab": vocab, "word": word})
        setVisibility();
    }

    return (
        <ModalBox visibility={visibility}>
            <Content style={{ margin: "0 0 0 0" }}>
                <h3 style={{ margin: "0 0 0 0" }}>Linked Data Vocabulary</h3>
                <hr style={{ marginBottom: "0" }} />
            </Content>

            <ColumnsContainer>
                <SelectColumn
                    columnName={"Vocabulary"}
                    options={vocabs}
                    onClick={setVocab}
                    activeCell={vocab}
                />
                <SelectColumn
                    columnName={"Word"}
                    options={words}
                    onClick={setWord}
                    activeCell={word}
                />
                <DocumentationArea doc={doc} />
            </ColumnsContainer>

            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    margin: "1em"
                }}
            >
                <Button onClick={setVisibility} color="link">Back</Button>
                <Button onClick={handleSetPredicate}>Confirm</Button>
            </div>
        </ModalBox>
    );
}
