import React from "react";
import styled from "styled-components";

import history from "../../history"

import OptionCard from "../Molecules/OptionCard";

const FlexContainer = styled.div`
    display: flex;
    flex-direction: row;
    justify-content: space-around;
`;

const MenuArea = styled.div`
    width: 50vw;
`;

const handleExportJSON = repositoryName => {
    window.open(window.API_ENDPOINT +
                    "/repository/" +
                    repositoryName +
                    "/exporter/json", "_blank")
};

export default function ExportMenuBox(props) {
    const cards = [
        {
            title: "Export into JSON file",
            subtitle:
                "Suitable for general use of scraped data eg: cross tabulation",
            img: <img src={require("../Lib/json-icon.png")} alt="JSON Export" />,
            onClick: (() => handleExportJSON(props.repositoryName))
        },
        {
            title: "Export into Linked Data",
            subtitle: "Interactively browse throught parsed data and linked data dictionary",
            color: "#3273DC",
            img: <img src={require("../Lib/link-icon.png")}  alt="LinkedData Export"/>,
            onClick: (() => {history.push("#/export/lod/" + props.repositoryName); window.location.reload()})
        },
        {
            title: "Export into MongoDB Insert",
            subtitle: "Data will be inserted into provided MongoDB Endpoint",
            color: "#00C1A2",
            img: <img src={require("../Lib/mongodb-icon.png")} alt="Mongodb Export"/>,
            onClick: (() => {history.push("#/export/mongodb/" + props.repositoryName); window.location.reload()})
        }
    ]
    const RenderedCard = cards.map(card => <OptionCard {...card} />);

    return (
        <FlexContainer>
            <MenuArea>{RenderedCard}</MenuArea>
        </FlexContainer>
    );
}
