import React from "react";
import styled from "styled-components";

const Column = styled.div`
    border-style: solid;
    border-width: 1px;
    width: 100%
    height: 100%
    margin: 1em 1em 0em;
    display: flex;
    flex-direction: column;
    overflow: scroll;
`;

const ColumnContainer = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 20%;
    flex-shrink: 0;
    margin: 1em 1em 0em;}
`;

const Cell = styled.div`
    height: 3em;
    line-height: 3em;
    background-color: ${props => (props.active ? "#FF9597" : "white")};
    &:hover {
        background-color: ${props => (props.active ? "#FF9597" : "#ededed")};
    }
    
F00
`;

const CellText = styled.div`
    margin-left: 1em;
`;

function CellItem(props) {
    const { data = "content", active=false } = props;

    return (
        <Cell onClick={props.onClick} active={active}>
            <CellText>{data}</CellText>
        </Cell>
    );
}

export default function SelectColumn(props) {
    const {
        columnName = "mockColName",
        options = ["option1", "option2"],
        activeCell = null,
        onClick = console.log
    } = props;

    return (
        <ColumnContainer>
            <b>{columnName}</b>
            <Column>
                {options.map((option, index) => (
                    <CellItem data={option} active={option === activeCell} onClick={() => {onClick(option)}}/>
                ))}
            </Column>
        </ColumnContainer>
    );
}
