import React, { useState } from "react";
import axios from "axios";
import styled from "styled-components";

import { Table } from "react-bulma-components";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";

const StyledButton = styled(FontAwesomeIcon)`
    font-size: 1.25em;
    cursor: pointer;
    margin: 0.25em 0.5em 0.25em 0.5em;
    color: "#4a4a4a"};
`;

const StyledTable = styled(Table)`
    height: 100%;
`;

const getTableData = (setTableData, repositoryName) => {
    axios
        .get(
            window.API_ENDPOINT +
                "/repository/" +
                repositoryName +
                "/rdf/rules/"
        )
        .then(response => {
            console.log(response.data);
            setTableData(response.data);
        })
        .catch(error => {
            console.log(error);
        });
};

const handleRemove = (index, repositoryName) => {
    if (window.confirm("Delete this rule?")) {
        axios
            .post(
                window.API_ENDPOINT +
                    "/repository/" +
                    repositoryName +
                    "/rdf/remove_rule/",
                    {index: index}
            )
            .then(response => {
                window.location.reload()
            })
            .catch(error => {
                console.log(error);
            });
    }
};

const TableBody = (tableResponse, repositoryName) => {
    return tableResponse.map(row => {
        return (
            <tr>
                <td style={{ textAlign: "center" }}>{row.s}</td>
                <td style={{ textAlign: "center" }}>{row.p}</td>
                <td style={{ textAlign: "center" }}>{row.o}</td>
                <td>
                    <StyledButton
                        icon={faTrash}
                        onClick={() => handleRemove(row.i, repositoryName)}
                    />
                </td>
            </tr>
        );
    });
};

export default function RulesTable(props) {
    const { repositoryName } = props;
    const [tableData, settableData] = useState(null);

    if (tableData === null) {
        getTableData(settableData, repositoryName);
        return "loading";
    }

    return (
        <div>
            <StyledTable>
                <thead>
                    <tr>
                        <th>Subject(s)</th>
                        <th>Predicate(s)</th>
                        <th>Object(s)</th>
                        <th>Remove</th>
                    </tr>
                </thead>
                <tbody>{TableBody(tableData, repositoryName)}</tbody>
            </StyledTable>
        </div>
    );
}
