import React, { useState } from "react";
import EditButton from "../Atoms/EditButton";
import EditMenu from "../Atoms/EditMenu";

export default function EditURLController(props) {
    const [showModalBox, setShowModalBox] = useState(false);
    const { baseurl, entityIdentifier, availableIdentifier, repositoryName } = props;

    return (
        <>
            <EditButton
                onClick={() => {
                    setShowModalBox(!showModalBox);
                }}
            />
            <EditMenu
                visibility={showModalBox ? "visible" : "hidden"}
                onClick={() => {
                    setShowModalBox(!showModalBox);
                }}
                baseurl={baseurl}
                entityIdentifier={entityIdentifier}
                availableIdentifier={availableIdentifier}
                repositoryName={repositoryName}
            />
        </>
    );
}
