import React, { useState } from "react";
import axios from "axios";
import styled from "styled-components";
import { Box, Content, Button } from "react-bulma-components";
import {
    Field,
    Control,
    Label,
    Input,
    Help
} from "react-bulma-components/lib/components/form";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPencilAlt } from "@fortawesome/free-solid-svg-icons";

import ObjectSelector from "../Atoms/ObjectSelector";
import LinkedDataVocabularyModal from "../Orgnaisms/LinkedDataVocabularyModal";

const ModalBoxContainer = styled.div`
    visibility: ${props => props.visibility || "visible"};
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
`;

const Background = styled.div`
    background: #4e4e4e;
    width: 100%;
    height: 100%;
    opacity: 0.9;
    position: absolute;
`;

const ModalBox = styled(Box)`
    width: 60%;
    z-index: 2;
`;

export default function RuleModal(props) {
    const {
        subject = "http://www.mock.com",
        availableIdentifier = ["1", "2"],
        entityIdentifier = "mock",
        repositoryName = "mock"
    } = props;

    // form value
    const [objectType, setObjectType] = useState("Literal(String)");
    const [objectColumn, setObjectColumn] = useState(
        availableIdentifier[0] || null
    );
    const [predicate, setPredicate] = useState(undefined);

    // vocab modal box
    const [displayVocabModal, setdisplayVocabModal] = useState(false);

    // the rule that is being created
    let newRule = {
        subject: subject + "/<" + entityIdentifier + ">",
        predicate: predicate,
        object: {
            objectType: objectType,
            objectColumn: objectColumn
        }
    };

    const handleExport = rule => {
        console.log(rule);
        axios
            .post(
                window.API_ENDPOINT +
                    "/repository/" +
                    repositoryName +
                    "/rdf/add_rule/",
                rule
            )
            .then(response => {
                window.location.reload();
            })
            .catch(error => {
                console.log(error);
            });
    };

    return (
        <ModalBoxContainer visibility={props.visibility}>
            <LinkedDataVocabularyModal
                visibility={displayVocabModal}
                setVisibility={() => setdisplayVocabModal(false)}
                setPredicate={setPredicate}
                repositoryName={repositoryName}
            />

            <Background onClick={props.onClick} />
            <ModalBox>
                <Content>
                    <h3>Add New Linked Data Rule</h3>
                    <hr />
                    {/* field 1 */}
                    <Field>
                        <Label>1. Subject</Label>
                        <Control>
                            <Input value={newRule.subject} disabled={true} />
                        </Control>
                        <Help>New links will be associated with this URI.</Help>
                    </Field>
                    <br />

                    {/* field 2  */}
                    <Field>
                        <Label>2. Predicate</Label>
                        <Control>
                            <Button onClick={() => setdisplayVocabModal(true)}>
                                <span className="icon">
                                    <FontAwesomeIcon icon={faPencilAlt} />
                                </span>
                                <span>Linked Data Vocabulary</span>
                            </Button>
                            {newRule.predicate && (
                                <div>
                                    Selected predicate:{" "}
                                    <mark>{newRule.predicate.word}</mark>
                                </div>
                            )}
                        </Control>
                        <Help>
                            Use interactive Linked Data Vocabulary to select
                            predicate
                        </Help>
                    </Field>
                    <br />

                    {/* field 3 */}
                    <Field>
                        <Label>3. Object</Label>
                        <ObjectSelector
                            objectType={objectType}
                            objectColumn={objectColumn}
                            setObjectType={setObjectType}
                            setObjectColumn={setObjectColumn}
                            availableObjectColumns={availableIdentifier}
                            baseURI={subject}
                        />
                        <Help>
                            Objects can either be Literal (plain string) or
                            Entity (can be linked)
                        </Help>
                    </Field>
                    <br />

                    {/* submit */}
                    <Field kind="group">
                        <Control>
                            <Button
                                onClick={() => {
                                    handleExport(newRule);
                                }}
                                disabled={
                                    !newRule["predicate"] ||
                                    !newRule["object"]["objectType"] ||
                                    !newRule["object"]["objectColumn"]
                                }
                            >
                                Submit
                            </Button>
                        </Control>
                        <Control>
                            <Button color="link" onClick={props.onClick}>
                                Cancel
                            </Button>
                        </Control>
                    </Field>
                </Content>
            </ModalBox>
        </ModalBoxContainer>
    );
}
