import React from "react";

export default function BaseURLText(props) {
    const { baseurl, entityIdentifier } = props;
    return (
        <>
            
            <u style={{ fontSize: "1.5em" }}>{baseurl}</u>
            <span style={{ fontSize: "1.75em" }}> / </span>
            <u style={{ fontSize: "1.5em" }}>{"<" + entityIdentifier + ">"}</u>
        </>
    );
}
